.input-item-container {
  width: 100%;
  height: 59px;
  padding: 10px;
  align-items: center;
  flex-direction: row;
  background-color: var(--white);
  border-radius: 8px;
  display: flex;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-top: 18px;
  .input {
    margin-left: 15px;
    font-family: 'Google Sans', sans-serif;
    font-size: 16px;
    color: var(--black);
    border: none;
    width: 100%;
  }

  .input:focus {
    border: none;
  }

  .input::placeholder {
    color: var(--text-placeholder);
    font-family: 'Google Sans', sans-serif;
  }
}
